.input {
  width: 40%;
  margin: 10px;
}
.section {
  display: flex;
  flex-direction: row;
  justify-content: center;
  width: 100%;
}

.tab {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 20%;
  border: 3px solid black;
  margin: 5px auto;
  padding: 5px;
  font-size: 16px;
  cursor: pointer;
}

.hide {
  display: none;
}

.resume {
  display: flex;
  flex-direction: row;
  justify-content: flex-end;
}

.container {
  display: flex;
  flex-direction: row;
}

img {
  width: 100%;
}

.data-tab {
  margin: 20px 50px 10px;
  font-size: 30px;
  font-weight: bold;
}
